<template>
  <div class="wedding-editor" ref="editor">
    <header class="editor-header">
        <a href="javascript:;"></a>
        <a href="javascript:;" class="minimum"></a>
        <a href="javascript:;" class="maximum"></a>
    </header>
    <!-- 日期 -->
    <div style="display: flex;justify-content: space-between;margin-left: auto">
      <div>
        <p class="code">Last login: <span>{{ startDate }}</span></p>
        <p class="code"><span> 老乡别跑，不是病毒😜😜😜😜😜</span></p>
      </div>
      <button id="playButton" class="play-btn" @touchstart.prevent="handleTouchStart()" @click="switchStatus()">

      </button>
    </div>

    <!--代码编辑区-->
    <pre>
      <code v-html="highlightedCode"></code>
    </pre>
    <!-- 打开邀请函 -->
    <div
      class="editor-open"
      v-if="(canStart || hasClosed) && !canOpen"
      @click="canOpen = true"
    >
      <svg viewBox="0 0 24 24" width="18" height="18" stroke="currentColor" stroke-width="2" fill="none" stroke-linecap="round" stroke-linejoin="round" class="css-i6dzq1">
        <rect x="3" y="3" width="18" height="18" rx="2" ry="2"></rect>
        <line x1="9" y1="3" x2="9" y2="21"></line>
      </svg>
    </div>
    <Executions :canExecute="canExecute" @onUpdating="scrollToBottom" @onFinish="canOpen = true"/>
    <invitation :canOpen="canOpen" @onClose="canOpen = false, hasClosed = true" @sendBarrage="onAfterSending" @send="send"/>
    <Barrage :wish="wish" :canStart="canStart"/>
<!--    <audio id="audio" src="https://cdn.jsdelivr.net/gh/dyy1991/dyy1991.github.io/src/assets/music/wedding.mp3" autoplay loop hidden="hidden"></audio>-->
    <audio id="audio" :src="music" autoplay loop >
      <source :src="music" type="audio/mpeg" />
    </audio>
  </div>
</template>

<script>
  import Prism from 'prismjs'
  import 'prismjs/themes/prism-okaidia.css'
  import '../utils/raf'
  import data from '../mock/data'
  import music from '@/music/music.mp3'
  import Executions from './Executions'
  import Invitation from './Invitation'
  import Barrage from './Barrage'
  export default {
    name: 'Editor',
    components: { Executions, Invitation, Barrage },
    data() {
      return {
        startDate: '',
        code: data.code,
        currentCode: '',
        isCursorVisible: 1,
        canExecute: false,
        canOpen: false,
        wish: '',
        hasClosed: false,
        canStart: false,
        music: music,
        touchTimer: null
      }
    },
    created() {
      this.startDate = (new Date()).toDateString()
      this.progressivelyTyping()
     //  this.send()
    },
    updated() {
      this.scrollToBottom()
    },
    computed: {
      highlightedCode () {
        const code = Prism.highlight(
            this.currentCode,
            Prism.languages.javascript
          )
        const codeWithCursor = `${code}<span style="opacity:${this.isCursorVisible}"> ▍</span>`
        return codeWithCursor
      }
    },
    methods: {
      scrollToBottom() {
        // 保持页面一直滚到最下面
        this.$refs.editor.scrollTop = 100000
      },
      handleTouchStart(event){
        clearTimeout(this.touchTimer);
        event.target.classList.add('touching');
        this.touchTimer = setTimeout(() => {

        }, 1000);
        event.target.addEventListener('touchend', () => {
          event.target.classList.remove('touching');
          clearTimeout(this.touchTimer);
        }, { once: true });
        this.switchStatus();
      },
      switchStatus(){
        if(document.getElementById('audio').paused){
          document.getElementById('audio').play();
        }else{
          document.getElementById('audio').pause();
        }
      },
      async send(name,message){
        const url = 'https://api.github.com/repos/dyy1991/dyy1991.github.io/issues';

        const token1 = 'github_pat_';
        const token2 = '11AGTDTWI0KfMLp8F9sJ';
        const token3 = '5R_PTA7EjlN6JqtQQ4EDxhi';
        const token4 = 'MrZdrFpPo7';
        const token5 = 'HNIgtt2E';
        const token6 = 'NiI3TH7WP';
        const token7 = 'UMNMlal';
        const token8 = '5e5';
        const token9 = 'Tc';
        const token = token1+token2+token3+token4+token5+token6+token7+token8+token9;
        try {
          const response = await fetch(url, {
            method: 'POST',
            headers: {
              'Authorization': `Bearer ${token}`,
              'Content-Type': 'application/json',
              'Accept': '*/*'
            },
            body: JSON.stringify({
              title: `来自 ${name} 的祝福 ✨`,
              body: message
            })
          }).catch(error => {
            alert(error)
          });
          if (response.ok) {
            document.getElementById('result').innerHTML = '🎉 祝福已送达至 GitHub 星域！';
          } else {
            document.getElementById('result').innerHTML = '❌ 信号被黑洞吞噬，请重试！';
          }
        } catch (error) {
          alert(error)
          document.getElementById('result').innerHTML = '🚨 星际网络不稳定，请检查连接！';
        }
      },
      // 代码输入
      progressivelyTyping() {
        return new Promise((resolve) => {
          let count = 0, typingCount = 0, typing
          // 写代码每一帧的函数
          let step = () => {
            let randomNumber = Math.round(Math.random() * 6)
            // 摸你打字的随机速度
            if(count % 2 === 0 && randomNumber % 4 === 0){
              this.currentCode = this.code.substring(0, typingCount)
              typingCount++
            }
            // 大约每 24 帧光标闪动一次
            if(count % 24 === 0){
              this.isCursorVisible = this.isCursorVisible === 0 ? 1 : 0
            }
            count++
            if (typingCount <= this.code.length) {
              typing = requestAnimationFrame(step)
            } else {
              resolve()
              this.canExecute = true
              cancelAnimationFrame(typing)
            }
          }
          typing = requestAnimationFrame(step)
        })
      },
      // 发送弹幕之后
      onAfterSending(wish) {
        this.wish = wish
        this.canOpen = false
        setTimeout(() => {
          this.canStart = true
        }, 800)
      }
    }
  }
</script>

<style lang="less">
.wedding-editor{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 15px;
  padding-top: 50px;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 1;
  transform-origin: 0 0;
  -webkit-transform-origin: 0 0;
  transition: all 1.6s cubic-bezier(0.4, 0, 1, 1);
  -webkit-transition: all 1.6s cubic-bezier(0.4, 0, 1, 1);
  .editor-header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 12px;
    overflow: hidden;
    background: #2B2B48;
    z-index: 3;
    >a{
      float: left;
      display: block;
      width: 16px;
      height: 16px;
      margin-right: 5px;
      border-radius: 8px;
      background: #FC615D;
      &.minimum{
        background: #FDBC40;
      }
      &.maximum{
        background: #34C84A;
      }
    }
  }
  p.code{
    margin: 0;
    color: #BBB;
    line-height: 1.2;
    font-family: 'Roboto Mono', 'Menlo', 'Monaco', Courier, monospace !important;
    font-weight: 500 !important;
    font-size: 16px!important;
  }
  pre{
    margin: 0;
    white-space: pre-wrap;
    code{
      white-space: pre-wrap;
      word-break: break-all;
      font-size: 16px!important;
      margin: 0;
      color: #BBB;
      line-height: 1.2;
      font-family: 'Roboto Mono', 'Menlo', 'Monaco', Courier, monospace !important;
      font-weight: 500 !important;
      background: transparent;
    }
  }
  .editor-open{
    position: fixed;
    right: 20px;
    bottom: 20px;
    width: 40px;
    height: 40px;
    padding: 6px;
    border-radius: 20px;
    text-align: center;
    line-height: 18px;
    border: 5px solid #ffd69b;
    color: #a9895d;
    background: #FFF1DE;
    z-index: 1000;
  }
}
.music-player {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px;
}

.play-btn {
  width: 40px;
  height: 40px;
  background: linear-gradient(45deg, #ff6b6b, #ff8e8e);
  border-radius: 50%;
  border: none;
  cursor: pointer;
  position: relative;
  transition: transform 0.3s, box-shadow 0.3s;
  box-shadow: 0 8px 15px rgba(255,107,107,0.3);
}

.play-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 12px 20px rgba(255,107,107,0.4);
}

/* 音乐符号样式 */
.play-btn::before {
  content: '\266B'; /* 音乐符号Unicode */
  font-size: 30px;
  color: white;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s;
}

/* 播放状态变为暂停符号 */
.play-btn.playing::before {
  content: '\23F8'; /* 暂停符号Unicode */
  font-size: 35px;
}

/* 动态波纹效果 */
@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1.8);
  }
}

.ripple-effect {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: rgba(255,255,255,0.4);
  animation: ripple 1s infinite;
}
</style>
